        ></el-date-picker>
      </div>
      <label class="label-icon" title="" @click="uploadData">
        <i class="iconfont icon-bda-requirement-old"></i>
      </label>
    </div>
    <div class="control-main-style" v-if="loadAllMainRes">
      <div class="control-main-qk">
        <div class="top-left">
