          <dispath-top v-if="tabIndex === 1"></dispath-top>
          <receipt-top v-if="tabIndex === 0"></receipt-top>
        </div>
        <div class="top-right" :class="'active' + tabIndex">
          <div class="top-right-title">
            <div class="top-right-title-content">
              <span @click="typeChange(0)"></span>
              <span @click="typeChange(1)"></span>
              <span @click="typeChange(2)"></span>
            </div>
          </div>

          <div class="top-right-content">
            <div class="top-right-left">
              <tab-change
                v-if="tabChangeStatus"
