              ></tab-change>
            </div>
            <div class="top-right-right">
              <kb-label v-if="kbRes" :type="tabIndex + 1 + ''"></kb-label>
            </div>
          </div>
        </div>
      </div>
      <div class="control-main-qk" style="padding: 0px">
        <business-process-bottom
