        ></business-process-bottom>
        <dispath-bottom v-if="tabIndex === 1"></dispath-bottom>
        <receipt-bottom v-if="tabIndex === 0"></receipt-bottom>
      </div>
    </div>
    <el-dialog :title="dialogTitle" :visible.sync="dialogVisible" width="600">
      <div class="el-dialog-style-main">
        <div class="header-filter">
          <span></span>
          <el-radio-group v-model="wdRadio" @change="handleRadioWd">
