            :value-format="wdRadio==='2'?'yyyyMM':'yyyy'"
            :placeholder="wdRadio==='2'?'':''"
            style="width: 150px"
          ></el-date-picker>
          <span></span>
          <el-select placeholder="" v-model="filterData.dataType">
            <el-option
              v-for="(v, k, i) in mbMap[tabIndex]"
              :value="k"
              :label="v"
              :key="i"
            ></el-option>
          </el-select>
          <el-button type="primary" @click="downloadMb" class="download-btn">
            <i class="el-icon-download"></i>
            
          </el-button>
          <el-upload
