    // upload-
    beforeAvatarUpload(file, fileList) {
      let $this = this;
      this.fileTemp = file.raw;
      if (fileList[0].status !== 'ready') {
        return false;
      }
      if (this.fileTemp) {
        if ((this.fileTemp.type == 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet')
          || (this.fileTemp.type == 'application/vnd.ms-excel')) {
          this.$confirm('  ' + this.filterData.date + '    ' + this.filterData.name + '  ' +
            '', '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
          }).then(() => {
            $this.checkExcel($this.fileTemp);
          }).catch(() => {
            return false;
          });
        } else {
          this.$message({
            type: 'warning',
            message: ''
          });
          this.fileList = [];
          return false;
        }
      } else {
        this.$message({
          type: 'warning',
          message: ''
        });
        return false;
      }
    },
    // 
    checkExcel(obj) {
      this.file = obj;
      var f = this.file;
      var reader = new FileReader();
      var outdata;
      let header;
      let $this = this;
      reader.readAsBinaryString = function (f) {
        var binary = "";
        var wb; //
        var reader = new FileReader();
        reader.onload = function () {
          var bytes = new Uint8Array(reader.result);
          var length = bytes.byteLength;
          for (var i = 0; i < length; i++) {
            binary += String.fromCharCode(bytes[i]);
          }
          var XLSX = require("xlsx");
          wb = XLSX.read(binary, {
            type: "binary"
          });
          outdata = XLSX.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]]);
          header = getHeaderRow(wb.Sheets[wb.SheetNames[0]]);
          let dqMbHeader = $this.fileHeaderMap[$this.filterData.dataType] ? $this.fileHeaderMap[$this.filterData.dataType] : [];
          let errorCount = 0;
          // 
          if (dqMbHeader.length > 0) {
            for (let i = 0; i < header.length; i++) {
              if (header[i] !== dqMbHeader[i]) {
                errorCount++;
              }
            }
            if (errorCount > 0) {
              $this.fileList = [];
              let _str = dqMbHeader.toString().replace(/,/g, '');
              $this.$confirm(' ' + $this.file.name + ' ' + $this.filterData.name + '' + _str + '', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'error'
              }).then(() => {
              }).catch(() => {
              });
            }
          }
          // 
          if (outdata.length !== 0) {
            let number = /^[0-9]*$/;
            let float = /^(?:0\.\d+|[01](?:\.0)?)$/;    // 1
            let z = 0;
            let _type = $this.filterData.dataType;
            for (let item of outdata) {
              z++;
              let c = 0;
              for (let _v of dqMbHeader) {
                c++;
                if (_type === '2') {
                  if (_v.indexOf('') > -1) {
                    if (item[_v]) {
