/**
 * Excel 
 * @module utils/excel
 */

import XLSX from 'xlsx';

function setAutoWidth(ws, data) {
    /* set worksheet max width per col */
    const colWidth = data.map(row => row.map(val => {
        /* if null/undefined */
        if (val === null) {
            return {'wch': 10};
            /* if chinese */
        } else if (val.toString().charCodeAt(0) > 255) {
            return {'wch': val.toString().length * 2};
        } else {
            return {'wch': val.toString().length};
        }
    }))
    /* start in the first row */
    let result = colWidth[0];
    for (let i = 1; i < colWidth.length; i++) {
        for (let j = 0; j < colWidth[i].length; j++) {
            if (result[j]['wch'] < colWidth[i][j]['wch']) {
                result[j]['wch'] = colWidth[i][j]['wch'];
            }
        }
    }
    ws['!cols'] = result;
}

function jsonToArray(key, jsonData) {
    return jsonData.map(v => key.map(j => {
        return v[j]
    }));
}

// fix data,return string
export const  fixData=(data)=>{
    let o = ''
    let l = 0
    const w = 10240
    for (; l < data.byteLength / w; ++l) o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w, l * w + w)))
    o += String.fromCharCode.apply(null, new Uint8Array(data.slice(l * w)))
    return o
}


// get head from excel file,return array
export const getHeaderRow = (sheet) => {
    const headers = []
    const range = XLSX.utils.decode_range(sheet['!ref'])
    let C
    const R = range.s.r
    /* start in the first row */
    for (C = range.s.c; C <= range.e.c; ++C) { /* walk every column in the range */
        const cell = sheet[XLSX.utils.encode_cell({c: C, r: R})]
        /* find the cell in the first row */
        let hdr = 'UNKNOWN ' + C // <-- replace with your desired default
        if (cell && cell.t) hdr = XLSX.utils.format_cell(cell)
        headers.push(hdr)
    }
    return headers
}


/**
 * 
 * @param {array} key 
 * @param {array} title 
 * @param {array} data 
 * @param {string} fileName 
 * @param {boolean} autoWidth 
 * @return {Promise<any>}
 */
export const write = ({key, data, title, fileName, autoWidth}) => {
    return new Promise(resolve => {
        const wb = XLSX.utils.book_new();
        const arr = jsonToArray(key, data);
        arr.unshift(title);
        const ws = XLSX.utils.aoa_to_sheet(arr);
        if (autoWidth) {
            setAutoWidth(ws, arr);
        }
        XLSX.utils.book_append_sheet(wb, ws, fileName);
        XLSX.writeFile(wb, fileName + '.xlsx');
        resolve()
    })
}

// table
function addTableRow(obj, arr, excludes) {
    Array.prototype.forEach.call(obj.rows, r => {
        let row = [];
        Array.prototype.forEach.call(r.cells, (c, idx) => {
            if (!excludes.includes(idx)) {
                row.push(c.innerText + '\x0a')
            }
        })
        arr.push(row)
    })
    return arr;
}

// el-table table
function getTableData(table, excludes) {
    if (!table) {
        throw new Error('')
    }
    let arr = [];
    let headerEl = table.querySelector('.el-table__header');
    let bodyEl = table.querySelector('.el-table__body');
    if (headerEl && bodyEl) {
        arr = addTableRow(headerEl, arr, excludes)
        arr = addTableRow(bodyEl, arr, excludes)
    } else if (table instanceof HTMLElement && table.tagName === 'TABLE') {
        arr = addTableRow(table, arr, excludes)
    } else {
        throw new Error('')
    }
    return arr;
}

/**
 *
 * @param {Array|Object} tables  tableel-table $el
 * @param {Array|String} sheetNames 
 * @param {String} fileName 
 * @param {Boolean} [autoWidth=true]
 * @param {Array|Array[]} [excludes] 
 */
export const writeFromTable = ({tables, sheetNames, excludes = [], fileName, autoWidth = true}) => {
    return new Promise((resolve) => {
        const wb = XLSX.utils.book_new();
        // excel)excel)
        if (tables instanceof Array && sheetNames instanceof Array) {
            tables.forEach((table, idx) => {
                let arr = getTableData(table, excludes[idx] || []);
                let ws = XLSX.utils.aoa_to_sheet(arr)
                if (autoWidth) {
                    setAutoWidth(ws, arr)
                }
                XLSX.utils.book_append_sheet(wb, ws, sheetNames[idx])
            })
        } else {
            let arr = getTableData(tables, excludes || []);
            let ws = XLSX.utils.aoa_to_sheet(arr)
            if (autoWidth) {
                setAutoWidth(ws, arr)
            }
            XLSX.utils.book_append_sheet(wb, ws, sheetNames)
        }
        XLSX.writeFile(wb, fileName + '.xlsx');
        resolve()
    })
}
/**
 * JSON
 * @param {Object|String} data 
 * @param {String} type  array  base64
 * @return {Object}
 */
export const read = (data, type) => {
    /* if type == 'base64' must fix data first */
    let workbook
    if (type === 'base64') {
        const fixedData = fixData(data)
        workbook = XLSX.read(btoa(fixedData), {type: 'base64'})
    } else {
        workbook = XLSX.read(data, {type: type});
    }
    const firstSheetName = workbook.SheetNames[0];
    const worksheet = workbook.Sheets[firstSheetName];
    const header = getHeaderRow(worksheet);
    const results = XLSX.utils.sheet_to_json(worksheet);
    return {header, results};
}

/**
 * 
 * @param {Object} file 
 * @param {object} options onloadstart / onprogress / onerror / onload
 * @return {Promise<any>}
 */
export function reader(file, options = {}) {
    const reader = new FileReader()
    reader.readAsArrayBuffer(file)
    return new Promise((resolve, reject) => {
        const opt = {
            ...options,
            onerror(e) {
                options.onerror && options.onerror(e)
                reject(e)
            },
            onload(e) {
                const reuslt = e.target.result
                options.onload && options.onload(reuslt)
                resolve(reuslt)
            }
        }
        Object.keys(opt).forEach(key => {
            reader[key] = opt[key]
        })
    })

}

export default {write, writeFromTable, read, reader}
